function toggleText(event) {
    if (event.target && event.target.className == 'Services__toggle') {
        let next = event.target.nextElementSibling;
        let previous = event.target.previousElementSibling;
        next.style.display == "none" 
        ? (next.style.display = "block", previous.innerHTML = "-")
        : (next.style.display = "none", previous.innerHTML = "+");
    }
}
document.addEventListener('click', toggleText, true);